
.Pagination{
    background-color: #373b69;
    border-radius: 15px;
    padding: 8px;
    color: white;
    width: max-content;
    margin: 0 auto;

    ul{
        list-style-type: none;
        display: flex;
        align-items: center;
        padding: 0;

        li{
            margin: 0 5px;
            cursor: pointer;
            font-size: 16px;
            font-weight: 500;
            
        }
    }
}